<template>
  <div id="app">
    <header>
    <h1>KIKK COMPASS</h1>
    <p class="sous-titre">WANDER ARROUND NAMUR </p>
    </header>
      <div>
        <div v-for="(category, categoryName) in eventData" :key="categoryName">
          <h2>{{ categoryName }}</h2>
          <a  v-for="event in category" :key="event.name" :href="event.link">{{ event.name }}</a>
        </div>
        <h2> </h2>
        <h2> </h2>
        <span class="program"> <a href="https://www.kikk.be/schedule/" target="_blank">(see the schedule)</a></span>
    </div>
    <h2> </h2>

    <footer>
      <p class="sous-titre">A DELIBERATE</p>
      <h1 style="letter-spacing: -2px;">DEOPTIMIZATION</h1>
      <p class="sous-titre">MADE BY <a href="https://heretique.fr" target="_blank">HÉRÉTIQUE</a></p>
    </footer>
  </div>
</template>

<script>
import axios from 'axios';

export default {
  data() {
    return {
      eventData: null
    };
  },
  created() {
    this.fetchEventData();
  },
  methods: {
    fetchEventData() {
  axios
    .get("/kikk-derive-vue/events.json")
    .then(response => {
      // Vérifie si la réponse est une chaîne de caractères et tente de la parser
      if (typeof response.data === 'string') {
        try {
          this.eventData = JSON.parse(response.data);
        } catch (e) {
          console.error('Error parsing JSON:', e);
        }
      } else {
        this.eventData = response.data;
      }
    })
    .catch(error => {
      console.error("Error fetching the event data:", error);
    });
}
  }
};
</script>

